import Vue from 'vue'
import App from './App.vue'
import login from './login.vue'
import register from './register.vue'
import car from './car.vue'
import classify from './classify.vue'
import pay from './pay.vue'
import pay1 from './pay1.vue'
import zhifu from './zhifu.vue'
import jie from './jieqi.vue'
import guan from './guanyu.vue'
import contact from './contact.vue'
import league from './league.vue'
import bus from 'vue-bus'

import VueRouter from 'vue-router'

Vue.use(VueRouter)
Vue.use(bus)

var router = new VueRouter({
	routes:[
	   {
	   	path:"/",
	   	component:car
	   },
	      {
	   	path:"/fen",
	   	component:classify
	   },
	      {
	   	path:"/zhi",
	   	component:pay,
	   	children:[
	   	   {
	   	   	path:'/',
	   	   	component:pay1
	   	   },
	   	   {
	   	   	path:'no3',
	   	   	component:zhifu
	   	   },
	   	   {
	   	   	path:'no1',
	   	   	component:zhifu
	   	   },
	   	   {
	   	   	path:'no2',
	   	   	component:zhifu
	   	   }
	   	]
	   },
	      {
	   	path:"/qi",
	   	component:jie
	   },
	      {
	   	path:"/guan",
	   	component:guan
	   },
	   {
	   	path:'/login',
	   	component:login
	   },
	   {
	   	path:'/register',
	   	component:register
	   },
	   {
	   	path:'/contact',
	   	component:contact
	   },
	   {
	   	path:'/league',
	   	component:league
	   }
	]
})


new Vue({
	router,
  el: '#app',
  render: h => h(App)
})



