<template>
    <div>
       <div class="title">
	       <h2>{{newdata.type}}</h2>
	       <p class="nep">{{newdata.ele}}</p>
	       <i :class="newdata.ico"></i>
       </div>
       <div class="title1">
	       	<h3>{{newdata.name}}</h3>
	       	<div v-for="(i,$index) in newdata.arrdata">
		       	<p class="pna">{{$index+1}}. {{ i.name}}</p>
		       	<p>{{i.text}}</p>
	       	</div>
       </div>
    </div>
</template>

<script>
	 export default {
	 	data:function(){
	 		return {
	 			arr:[
	 			  {
	 			  	type:'在线支付',
	 			  	ele:'Online Payment',
	 			  	ico:'glyphicon glyphicon-hand-down',
	 			  	name:'支付宝卡使用秘籍',
	 			  	arrdata:[
	 			  	          {name:'支付宝卡可以与其他支付方式一起使用吗？',
	 			  	             text:"可以和银行卡、集分宝、红包等一起使用。 若发生退货时，使用支付宝卡支付的资金退回卡帐户，不予提现。退款顺序为：银行卡、余额、预付卡、红包、集分宝。"
	 			  	             },
	 			  	             {name:'是否可以添加多张支付宝卡？',
	 			  	             text:"个支付宝帐户可以添加多张支付宝卡，无数量限制。"
	 			  	              },
	 			  	              {
	 			  	              name:'支付宝购物卡和支付宝卡的区别是什么？',
	 			  	              text:'支付宝购物卡是支付宝在海外发行的预付卡。 卡号前六位为800000、800100、800200、800300的为支付宝购物卡，卡号前六位非此号段的为支付宝。'
	 			  	              }
	 			  	          ]
	 			  },
	 			  {
	 			  	type:'银行转账',
	 			  	ele:'Online Payment',
	 			  	ico:'glyphicon glyphicon-hand-down',
	 			  	name:'网上银行付款常见问题',
	 			  	arrdata:[
	 			  	          {name:'网上银行付款跟支付宝快捷支付有什么区别？',
	 			  	             text:"支付宝快捷支付是支付宝联合各大银行推出的全新支付方式，开通后只需在支付宝端输入密码即可完成付款， 无需跳转网银页面，也无需使用U盾，跟网上银行是不同的付款方式。立即开通快捷支付。"
	 			  	             },
	 			  	             {name:'网上银行支付有限额么？',
	 			  	             text:"有限额。每家银行都有各自的限额规定，查看详情。"
	 			  	              }
	 			  	          ]
	 			  },
	 			  {
	 			  	type:'货到付款',
	 			  	ele:'Online Payment',
	 			  	ico:'glyphicon glyphicon-hand-down',
	 			  	name:'货到付款常见问题',
	 			  	arrdata:[
	 			  	          {name:'使用货到付款，一般需要多长时间才能收到？',
	 			  	             text:"在您选择货到付款方式购买宝贝后，卖家接到订单会在线下单通知物流取货，物流公司揽件成功录单后，交易状态会从“等待卖家发货”变更为“卖家已发货”，运送时间会根据物流公司的不同而有所不同，一般需要2-7天，建议您关注哦，具体可以联系卖家咨询。"
	 			  	             }
	 			  	          ]
	 			  	}
	 			],
	 			metypes:'在线支付'
	 		}
	 	},
	 	mounted:function(){
	 		this.$bus.$on('me',(a)=>{
	 		   this.metypes = a
	 		})
	 	},
	 	computed:{
	 		newdata(){
	 			for(var i=0;i<this.arr.length;i++){
	 				if(this.arr[i].type == this.metypes){
	 					return this.arr[i]
	 				}
	 			}
	 		}
	 	}
	 }
</script>

<style>
</style>


